import React from 'react'
import './style.css';
// icon


import {BsBellFill} from 'react-icons/bs'
import {MdLanguage} from 'react-icons/md'
import {IoIosArrowDown} from 'react-icons/io'

function HeaderPresenter() {
    return (
        <div id= "header">
            <div id="logo-div">
                <img id="logo" src={"/assets/hprobot-logo/hprobot-logo-1line.png"} alt="login-logo-img" />
            </div>
            <div id="language-div">
                <MdLanguage id="language-icon" />
                <div>한국어</div>
                <IoIosArrowDown id="arrow-icon" />
            </div>
            <div id="alram-div">
                <BsBellFill className="icon" />
            </div>
        </div>
    )
}

export default HeaderPresenter
