import React from 'react'
import {MdDashboard} from 'react-icons/md'
import './style.css';
import {DoughnutTypeOrder, LineTimeOrder, DoughnutAvailableRobot} from './Chart'
function DashboardPresenter() {

    return (
        <div>
            <div className="content" id="dashboard">
            <div className="content-title"><MdDashboard className="icon"/>대시보드</div>
            <div className="grid-container">
                    <div className="grid-item" id="item-store">
                        <div className="grid-title">매장 운영 지표</div>
                        <div className="sub-grid-container">
                            <div className="sub-grid-item" id="today-order">
                                <div className="sub-grid-title">오늘 주문 건수</div>
                                <div>
                                    <span className="order-count">50</span>
                                    <span className="gun">건</span>
                                </div>
                            </div>
                            <div className="sub-grid-item" id="accumulate-order">
                            <div className="sub-grid-title">누적 주문 건수</div>
                            <div className="period-div">
                                <p className="week">주간</p>|<p className="month">월간</p>
                            </div>
                            <div>
                                    <span className="order-count">310</span>
                                    <span className="gun">건</span>
                                </div>
                            </div>
                            <div className="sub-grid-item" id="type-order">
                            <div className="sub-grid-title">배달/홀 주문현황 </div>
                            <div className="period-div">
                                <p className="week">주간</p>|<p className="month">월간</p>
                            </div>
                            <div className="doughnut-graph"><DoughnutTypeOrder/></div>
                            <div className="value" id="delivery">
                                <div className="type">배달</div>
                                <div className="count">540건</div>
                                <div className="percent">(56%)</div>
                            </div>
                            <div className="value" id="hall">
                                <div className="type">홀</div>
                                <div className="count">540건</div>
                                <div className="percent">(56%)</div>
                            </div>
                            </div>
                            <div className="sub-grid-item" id="time-order">
                            <div className="sub-grid-title">시간대별 주문현황</div>
                            <div className="period-div">
                                <p className="week">주간</p>|<p className="month">월간</p>
                            </div>
                            <div className="line-graph"><LineTimeOrder id="LineTimeOrder"/></div>

                            </div>
                        </div>
                    </div>
                    <div className="grid-item" id="item-robot">
                        <div className="grid-title">서빙로봇 운영 지표</div>
                        <div className="sub-grid-container">
                            <div className="sub-grid-item" id="available-robot">
                            <div className="sub-grid-title">현재 사용 가능한 로봇 수</div>
                                <div className="doughnut-graph"><DoughnutAvailableRobot/></div>
                                <div id="labels">
                                    <div id="yes">
                                        <div id="text">사용가능</div>
                                        <div id="color"></div>
                                    </div>
                                    <div id="no">
                                        <div id="text">사용불가</div>
                                        <div id="color"></div>
                                    </div>
                                </div>
                            </div>
                            <div className="sub-grid-item" id="statistics-robot">
                                <div className="statistics-item" id="time">
                                    <div className="title">로봇이 움직인 시간</div>
                                    <div className="value">333h</div>
                                </div>
                                <div className="statistics-item" id="distance">
                                    <div className="title">로봇이 움직인 거리</div>
                                    <div className="value">5km</div>
                                </div>
                                <div className="statistics-item" id="average">
                                    <div className="title">평균 서빙 소요 시간 </div>
                                    <div className="value">22m</div>
                                </div>
                                <div id="last-7days">(최근 7일)</div>
                            </div>
                        </div>
                        </div>
                </div>
            </div>
        </div>
    )
}

export default DashboardPresenter
